body {
  font-family: Arial, Helvetica, sans-serif,"微軟正黑體";
  font-size: 24px;
  line-height: 1.5;
  font-style: italic; }

header {
  justify-content: center;
  align-items: center;
  background-image: url("../images/banner.png");
  height: 958px;
  background-size: cover;
  background-position: center center;
  font-weight: bold; }
  header .title {
    line-height: 106px;
    font-size: 80px; }
  header .subtitle {
    line-height: 42px; }
  header .s-icon {
    justify-content: space-between; }

.about {
  font-weight: bold; }
  .about h2 {
    font-size: 64px;
    line-height: 84px; }
  .about .aboutme li:nth-child(-n+2) {
    line-height: 42px; }
  .about .work-ex {
    border-top: 1px solid #000;
    width: 384px;
    margin: 0 auto; }
    .about .work-ex li {
      line-height: 42px; }
    .about .work-ex li::before {
      content: "";
      display: inline-block;
      width: 22px;
      height: 22px;
      margin-right: 16px;
      background-color: #891818;
      border-radius: 50%; }

.myBrand {
  background-color: #000; }
  .myBrand h2 {
    font-size: 68px;
    line-height: 84px; }
  .myBrand h3 {
    line-height: 42px; }
  .myBrand p {
    line-height: 30px;
    font-size: 20px; }
  .myBrand .brandimg {
    flex-wrap: wrap;
    justify-content: space-between; }
    .myBrand .brandimg li:first-child {
      margin-right: auto;
      margin-left: auto; }

.otherWorks h2 {
  font-size: 64px;
  line-height: 84px; }

.otherWorks p {
  font-size: 20px;
  line-height: 30px; }

.otherWorks .mywork {
  flex-wrap: wrap;
  justify-content: space-between; }
  .otherWorks .mywork li {
    margin-bottom: 48px; }
  .otherWorks .mywork h4 {
    font-size: 20px;
    margin-top: 16px; }

footer {
  background-color: #000; }
  footer h2 {
    font-size: 64px;
    line-height: 84px; }
